import Section from "../../components/Section/Section";
import Title from "../../components/Title/Title";
import Text from "../../components/Text/Text";
import "./Presenters.scss";
import Bubble from "../../components/Bubble/Bubble";
import Button from "../../components/Button/Button";
import { useQuerySubscription } from "react-datocms";

const Presenter = (props) => {
    return (
        <div className="presenter-card">
            <div
                className="presenter-img"
                style={{
                    backgroundImage: "url('" + props.imageUrl + "')",
                }}
            >
                <Bubble
                    smallText
                    darkText
                    title={props.name}
                    subtitle={`${props.title}, ${props.company}`}
                    size="large"
                    shadow
                    color={props.highlight ? "primary" : "light"}
                    corners={[props.right ? "bottom-right" : "top-left"]}
                ></Bubble>
            </div>
        </div>
    );
};

const Presenters = () => {
    const { status, error, data } = useQuerySubscription({
        enabled: true,
        query: `
		  query AppQuery {
 					allSpeakers(
						filter: {
							highlighted: { eq: true }
						}
						orderBy: [name_ASC]) {
   			 			name
						slug
						title
						company
						image {
							url
						}
  					}
		}`,
        variables: { first: 10 },
		token: "3331fc3477e7df4b7cb85836c2a684",
    });

    const statusMessage = {
        connecting: "Connecting to DatoCMS...",
        connected: "Connected to DatoCMS, receiving live updates!",
        closed: "Connection closed",
    };

    return (
        <Section id="presenters" container placeholder>
            {error && (
                <div>
                    <h1>Error: {error.code}</h1>
                    <div>{error.message}</div>
                    {error.response && (
                        <pre>{JSON.stringify(error.response, null, 2)}</pre>
                    )}
                </div>
            )}
            

            <Title>
                <span className="highlight text-uppercase">Előadóink</span>
            </Title>
            <Text subtitle>
                Az IOK 2022 rendezvényünkön olyan előadókat hívtunk meg, akiknek
                komoly mondanivalójuk van az informatikai oktatás jelenéről és
                jövőjéról.
            </Text>
            <div className="presenters-grid">

				{

				data && (
					
					data.allSpeakers.map((speaker, index) => (
						<Presenter
							key={speaker.slug}
							
							name={speaker.name}
							highlight={index===0}
							title={speaker.title}
							company={speaker.company}
							imageUrl={speaker.image.url}
						/>

					))
				)}
            </div>

            <Button>További előadóink</Button>
        </Section>
    );
};

export default Presenters;
