import { useEffect } from "react";
import Section from "../../components/Section/Section";
import Text from "../../components/Text/Text";
import Title from "../../components/Title/Title";
import Clock from "../../icons/Clock";
import Coffee from "../../icons/Coffee";
import "./Schedule.scss";
import { useQuerySubscription } from "react-datocms";

const ScheduleItem = (props) => {
    return (
        <div className="shcedule-item">
            <div
                className="image"
                style={{
                    backgroundImage: "url('" + props.imageUrl + "')",
                }}
            ></div>
            <div className="time">
                <Clock />
                {props.time}
            </div>
            <div className="title">{props.title}</div>
            <div className="presenter">{props.presenter}</div>
        </div>
    );
};

const Separator = (props) => {
    return (
        <div className="separator">
            <div className="separator-text">{props.children}</div>

            <div className="line" />
        </div>
    );
};

const Schedule = () => {
    const { status, error, data } = useQuerySubscription({
        enabled: true,
        query: `
		query MyQuery {
			allStages {
			  name
			  schedule {
				start
				title
				speaker {
				  company
				  name
				  title
				  image {
					url
				  }
				}
			  }
			}
		  }
		`,
        variables: { first: 10 },
        token: "3331fc3477e7df4b7cb85836c2a684",
    });

    const statusMessage = {
        connecting: "Connecting to DatoCMS...",
        connected: "Connected to DatoCMS, receiving live updates!",
        closed: "Connection closed",
    };

    const selectTab = (e) => {
        const offset =
            window.scrollX +
            e.target.getBoundingClientRect().left -
            e.target.parentElement.getBoundingClientRect().left -
            2;
        const width = e.target.offsetWidth + 0;
        const tabButtons = document.getElementsByClassName("tab-button");
        [...tabButtons].map(
            (tabButton) =>
                (tabButton.className = tabButton.className.replace(
                    "selected",
                    ""
                ))
        );
        const selector = document.getElementsByClassName("selector")[0];

        e.target.className += " selected";
        selector.style.left = offset + "px";
        selector.style.width = width + "px";

        const targetIndex = Number(e.target.dataset.index);
        const allSessions = [...document.getElementsByClassName("sessions")];
        allSessions.map((sessions, index) => {
            sessions.className = sessions.className
                .replace(" right", "")
                .replace(" left", "")
                .replace(" selected", "");

            if (index === targetIndex) {
                sessions.className += " selected";
                document.getElementsByClassName(
                    "sessions-container"
                )[0].style.height =
                    sessions.getBoundingClientRect().height + "px";
            }
            if (index < targetIndex)
                sessions.className =
                    sessions.className.replace(" right", "") + " left";
            if (index > targetIndex)
                sessions.className =
                    sessions.className.replace(" left", "") + " right";

            return null;
        });
    };

    useEffect(() => {
        document.getElementsByClassName("tab-button")[0].click();
        //console.log(document.getElementsByClassName('tab-button')[0])
    }, []);

    console.log("data", data);

    return (
        <Section container placeholder id="schedule">
            <Title>
                Konferencia <span className="highlight">PROGRAM</span>
            </Title>
            <Text subtitle>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Rem at
                repudiandae assumenda.
            </Text>
            {data &&
                data.allStages[0].schedule.map((presentation, index) => {
                    if (presentation.speaker[0])
                        return (
                            <ScheduleItem
                                title={presentation.title}
                                time={presentation.start.substring(11, 16)}
                                presenter={`${presentation.speaker[0].name}, ${presentation.speaker[0].title}, ${presentation.speaker[0].company}`}
                                imageUrl={presentation.speaker[0].image.url}
                            />
                        );
                    else
                        return (
                            <Separator>
                                <Coffee />
                                {presentation.title}
                            </Separator>
                        );
                })}


            <div className="tabs-container">
                <h3>Szekció előadások</h3>
                <div className="tabs">
                    {/* <div className="tab-button selected">SZAKKÉPZÉS 4.0, ITMP ÉS NETACAD</div>
				
				<div className="tab-button">DIGITÁLIS KULTÚRA</div>
				<div className="tab-button">INFORMATIKAI KÉPZÉS A FELSŐOKTATÁSBAN</div> */}
                    <div className="selector"></div>
                    <div
                        className="tab-button"
                        onClick={selectTab}
                        data-index={0}
                    >
                        "A" szekció
                    </div>
                    <div
                        className="tab-button"
                        onClick={selectTab}
                        data-index={1}
                    >
                        "B" szekció
                    </div>
                    <div
                        className="tab-button"
                        onClick={selectTab}
                        data-index={2}
                    >
                        "C" szekció
                    </div>
                    <div
                        className="tab-button"
                        onClick={selectTab}
                        data-index={3}
                    >
                        "D" szekció
                    </div>
                </div>
                <div className="sessions-container">
                    <div className="sessions">
                        <ScheduleItem
                            title="A AAAA"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                    </div>
                    <div className="sessions">
                        <ScheduleItem
                            title="BBBBBB"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                    </div>
                    <div className="sessions">
                        <ScheduleItem
                            title="CCCCCCCCCC"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                    </div>
                    <div className="sessions">
                        <ScheduleItem
                            title="DDDDDDDDD"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                        <ScheduleItem
                            title="Köszöntő"
                            time="09:00 - 09:15"
                            presenter="Sisák Zoltán, elnök, HTTP Alapítvány"
                        />
                    </div>
                </div>
            </div>
        </Section>
    );
};

export default Schedule;
