import Section from "../../components/Section/Section"
import Text from "../../components/Text/Text"
import Title from "../../components/Title/Title"
import './Sponsors.scss'

const Sponsor = (props) => {
	return <div className="sponsor"><img src={props.image} alt={props.name} /></div>
}

const Sponsors = () => {
	return <Section container placeholder id="sponsors">
		<Title>Az IOK 2022 <span className="highlight">TÁMOGATÓI</span></Title>
		<Text subtitle>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Facilis illo repudiandae provident eligendi sapiente ducimus, explicabo numquam adipisci laudantium soluta, consequuntur nulla totam, natus ipsam?</Text>
		<div className="sponsor-grid">
			{(Array(8).fill(1)).map((k,i) => <Sponsor key={i} image="/assets/img/sponsors/google.png" />)}
		</div>
	</Section>
}

export default Sponsors